import { createSelector } from '@reduxjs/toolkit';

// Selector pentru a obine lista de contacte:
export const selectContacts = state => state.contacts.items;

// Selector pentru a obine filtrul:
export const selectFilter = state => state.filter;

// Selector pentru a obine eroarea asociat contactelor:
export const selectError = state => state.contacts.error;

// Selector pentru a verifica dac exist o ncrcare n curs pentru contacte:
export const selectIsLoading = state => state.contacts.isLoading;

// ==== Selector pentru a filtra contactele n funcie de irul de cutare: ====

// Se definete si se exporta un selector numit 'selectFilteredContacts' utiliznd createSelector:
export const selectFilteredContacts = createSelector(
  [selectContacts, selectFilter], // Se specific inputurile pentru selector (lista de contacte i filtrul)

  // Funcie de filtrare a contactelor:
  (contacts, filter) => {
    // Se returneaza o list de contacte filtrat pe baza irului de cutare:
    return contacts.filter(contact =>
      contact.name.toLowerCase().includes(filter.toLowerCase())
    );
  }
);

/** Pentru a obine contactele filtrate, cel mai bine este s folosim `createSelector`. Astfel, codul nostru din componenta `contactsList` va fi mai curat, iar selectorul creat cu ajutorul `createSelector` va fi **memorat**, ceea ce nseamn c recalculeaz contactele cnd se schimb valorile `state.contacts` sau `state.filter`, dar nu i atunci cnd se fac modificri n alte pri (independente) ale arborelui. */
